import * as Immutable from 'immutable';
import { AnimalModel } from '../../models';
export const initialState = {
    animal: new AnimalModel(),
    animals: [],
    openFormAddNewAnimal: false,
    errorData: '',
    successData: '',
    inProcess: false,
};
//Immutable State.
export class GeneralState extends Immutable.Record(initialState) {
    // Success METHODS
    setError(errorData) {
        return this.set("errorData", errorData);
    }
    setSuccess(successData) {
        return this.set("successData", successData);
    }
    setInProcess(inProcess) {
        return this.set("inProcess", inProcess);
    }
    // Project METHODS 
    setOpenFormAddNewAnimal(openFormAddNewAnimal) {
        return this.set("openFormAddNewAnimal", openFormAddNewAnimal);
    }
    getAllAnimals(animals) {
        return this.set("animals", animals);
    }
    addNewAnimal(animal) {
        return this.set("animal", animal);
    }
}
