import React from 'react';
import { connect } from 'react-redux';
import { addNewAnimal, setOpenFormAddNewAnimal } from '../../redux/actions';
import PropTypes from 'prop-types';
import { translationsEnum } from '../../translations/Translations';
import { AnimalModel } from '../../models';
function mapStateToProps(state) {
    return {
        AddItem_LS: state.projectsReducer.animal,
        openFromAddNewAnimal_LS: state.projectsReducer.openFromAddNewAnimal,
    };
}
//Map the actions to the properties of the Component.
const mapDispatchToProps = (dispatch) => ({
    AddItem_LS: (animal) => {
        dispatch(addNewAnimal(animal));
    },
    openFromAddNewAnimal_LS: (open) => {
        dispatch(setOpenFormAddNewAnimal(open));
    }
});
class FormNewAnimal extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            animal: new AnimalModel(),
            openFromAddNewAnimal: false
        };
    }
    ;
    OnChangeField(fieldName, target) {
        let auxModel = this.state.animal;
        auxModel[fieldName].value = target.value;
        this.setState({
            animal: auxModel
        });
    }
    AddNewAnimal(animal) {
        this.props.AddItem_LS(animal);
        this.props.openFromAddNewAnimal_LS(false);
        this.setState({ animal: new AnimalModel() });
    }
    render() {
        return (React.createElement("div", null,
            React.createElement("label", null, "Animal name"),
            React.createElement("input", { type: "text", onChange: () => this.OnChangeField("Name", event.target), placeholder: this.context.t(translationsEnum.PLACEHOLDER_NAME), value: this.state.animal.Name.value }),
            React.createElement("label", null, "Link photo animal"),
            React.createElement("input", { type: "text", onChange: () => this.OnChangeField("UrlPhoto", event.target), placeholder: this.context.t(translationsEnum.PLACEHOLDER_URLPHOTO), value: this.state.animal.UrlPhoto.value }),
            React.createElement("label", null, "Kingdom"),
            React.createElement("input", { type: "text", onChange: () => this.OnChangeField("Kingdom", event.target), placeholder: this.context.t(translationsEnum.PLACEHOLDER_KINGDOM), value: this.state.animal.Kingdom.value }),
            React.createElement("label", null, "Class"),
            React.createElement("input", { type: "text", onChange: () => this.OnChangeField("Class", event.target), placeholder: this.context.t(translationsEnum.PLACEHOLDER_CLASS), value: this.state.animal.Class.value }),
            React.createElement("label", null, "Conservation status"),
            React.createElement("input", { type: "text", onChange: () => this.OnChangeField("ConservationStatus", event.target), placeholder: this.context.t(translationsEnum.PLACEHOLDER_CONSERVATION_STATUS), value: this.state.animal.ConservationStatus.value }),
            React.createElement("label", null, "Region"),
            React.createElement("input", { type: "text", onChange: () => this.OnChangeField("Region", event.target), placeholder: this.context.t(translationsEnum.PLACEHOLDER_REGION), value: this.state.animal.Region.value }),
            React.createElement("label", null, "Short info"),
            React.createElement("textarea", { onChange: () => this.OnChangeField("ShortInfo", event.target), placeholder: this.context.t(translationsEnum.PLACEHOLDER_SHORT_INFO), value: this.state.animal.ShortInfo.value }),
            React.createElement("label", null, "Long info"),
            React.createElement("textarea", { onChange: () => this.OnChangeField("LongInfo", event.target), placeholder: this.context.t(translationsEnum.PLACEHOLDER_LONG_INFO), value: this.state.animal.LongInfo.value }),
            React.createElement("button", { onClick: () => this.AddNewAnimal(this.state.animal) }, "Add animal")));
    }
}
//Add ContextTypes
FormNewAnimal.contextTypes = {
    t: PropTypes.func.isRequired
};
export default connect(mapStateToProps, mapDispatchToProps)(FormNewAnimal);
